// 引入tabbar
import tabbar from "@/components/common-tabbar/common-tabbar";
import testCode from '@/components/testCode/testCode';
export default {
	data() {
		return {
			phoneMaskShow: false,
			hideBox: true,
			hideBox1: true,
			hideTips:false,
			hideTips1:false,
			type:0,
			type1:false,
			isPwd:'',
		};
	},
	methods: {
		backPage() {


















			uni.navigateBack(1)
		},
		// 前往个人资料
		goPersonal() {
			uni.navigateTo({
				url: "../personalData/personalData"
			})
		},
		// 前往更换页面
		goChangePhone() {
			if(getApp().globalData.isPwd){
				this.hideBox1 = false;
			}else{
				uni.showToast({
					title:'请先设置交易密码'
				})
			}
			
		},
		// 前往登录密码
		goLoginPassword() {
			uni.navigateTo({
				url: "../loginPassword/loginPassword"
			})
		},
		// 前往设置交易密码
		goSetPwd(){
			uni.navigateTo({
				url:"../setTransactionPwd/setTransactionPwd"
			})
		},
		// 前往交易密码
		goTransactionPassword() {
			uni.navigateTo({
				url: "../transactionPassword/transactionPassword"
			})
		},
		// 前往找回密码
		goPasswordBack(){
			uni.navigateTo({
				url:"../passwordback/passwordback"
			})
		},
		// 前往实名认证
		goCertification() {
			uni.navigateTo({
				url: "../certification/certification"
			})
		},
		// 确认注销
		submit() {
			// 关闭确认弹窗
			this.phoneMaskShow = false;
			// 弹出交易密码验证
			this.hideBox1=false;
			
		},
		// 获取输入登录密码
		getPwd(val) {
			if(val==888888){
				this.hideBox = true;
				if(this.type===2||this.type===0){
					this.phoneMaskShow=true;
					uni.reLaunch({
						url:"../login/login"
					})
				}
			}else{
				this.hideTips = true;
			}
		},
		// 获取输入交易密码、
		getPwd1(val){
			if(val==getApp().globalData.pwd){
				this.hideBox1 = true;
				if(this.type1){
					// 前往修改手机号
					uni.navigateTo({
						url: "../changePhone/changePhone"
					})
				}else{
					uni.removeStorage({
						key:'saveStata'
					})
					uni.removeStorage({
						key:'name',
						success:()=>{
							uni.reLaunch({
								url:'../login/login'
							})
						}
					})
					
				}
				
			}else{
				this.hideTips1 = true;
			}
		},
		// 关于我们
		goAbout(){
			uni.navigateTo({
				url:'../about/about'
			})
		},
		// 退出登录
		outLogin(){
			uni.reLaunch({
				url:"../login/login"
			})
		}
	},
	components: {
		tabbar,
		testCode
	},
	onLoad() {
		console.log(getApp().globalData)
		this.isPwd = getApp().globalData.isPwd;
	}
}
